/* reset css */
a{
    text-decoration:none;
    color:inherit;
}
body{
    /* background-color: #082138; */
}
#main-menu, #sub-menu {
    float: right;
    margin: 0;
    padding: 0;
    list-style-type: none;
    font-size: 15pt;
}


#main-menu > li {
    float:left;
    color: #395D7F;
    margin-right:30px;
}
#main-menu > li > a{
    display: block;
    padding: 6px 16px;
    text-align: center;
}

/* event css */
#sub-menu {
    opacity:  0;
}
#main-menu > li:hover #sub-menu {
    opacity:  1;
}
#main-menu > li #sub-menu li {
    margin-top:5px;
    font-size: 12pt;
}
#main-menu > li #sub-menu li:hover {
    color: white;
    font-weight: bolder;
}
#main-menu .clicked {
    color: #FF9599;
    font-weight: bolder;
}
#main-menu .no-cliked {
    color: #395D7F;
    font-weight: normal;
}

#circle {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    margin: 0px auto;
    background-color: white;
    margin-top: -12px;
}